}

* {
  box-sizing: border-box;
  margin: 0;
}

html, body {
  font-family: Montserrat, sans-serif;
  font-size: 10px;
}

h1, h2, h3 {
  font-family: Montserrat, sans-serif;
}

a {
  color: #2d2c2c;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.logo {
  width: 14.7rem;
  height: 11.4rem;
}

@media (max-width: 1199px) {
  .logo {
    width: 9.4rem;
    height: 7.8rem;
  }
}

@media (max-width: 767px) {
  .logo {
    width: 7.7rem;
    height: 5.3rem;
  }
}

.login {
  width: 3rem;
  height: 3rem;
  margin-right: 4rem;
}

@media (max-width: 767px) {
  .login {
    width: 1.7rem;
    height: 1.7rem;
  }
}

.step {
  text-align: center;
  width: 45%;
  height: 13rem;
  border: 4px solid #50dbd5;
  border-radius: 1.5rem;
  flex-direction: column;
  justify-content: center;
  padding: 0 3.5rem;
  font-size: 2.2rem;
  font-weight: 400;
  line-height: 140%;
  display: flex;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, .25);
}

@media (max-width: 1199px) {
  .step {
    background-color: #fff;
  }
}

.step strong {
  font-weight: 600;
}

.plus {
  border: 3px solid #0010e2;
  border-radius: 1.5rem;
  flex-grow: 1;
  flex-basis: 29%;
  margin-left: 6rem;
  padding: 4.2rem 6rem;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, .25);
}

@media (max-width: 1199px) {
  .plus {
    flex-basis: 45%;
    margin-left: 3.2rem;
    padding: 3rem;
  }
}

@media (max-width: 767px) {
  .plus {
    width: 100%;
    margin: 0 auto;
    padding: 2.8rem 4rem;
  }
}

.plus__title {
  color: #2d2c2c;
  margin-bottom: 3.2rem;
  font-size: 2.4rem;
  font-weight: 600;
  line-height: 140%;
}

@media (max-width: 1199px) {
  .plus__title {
    margin-bottom: 2rem;
    font-size: 2.2rem;
  }
}

@media (max-width: 767px) {
  .plus__title {
    font-size: 1.6rem;
  }
}

.plus__text {
  color: #535353;
  font-size: 2.2rem;
  line-height: 140%;
}

@media (max-width: 1199px) {
  .plus__text {
    font-size: 2rem;
  }
}

@media (max-width: 767px) {
  .plus__text {
    font-size: 1.4rem;
  }
}

.plus:first-child {
  margin-left: 0;
}

@media (max-width: 1199px) {
  .plus:nth-child(2n+3) {
    margin-left: 0;
  }
}

@media (max-width: 767px) {
  .plus:nth-child(2n+3) {
    margin: 0 auto;
  }
}

.plus:nth-child(4n+4) {
  margin-left: 0;
}

@media (max-width: 1199px) {
  .plus:nth-child(4n+4) {
    margin-left: 3.2rem;
  }
}

@media (max-width: 767px) {
  .plus:nth-child(4n+4) {
    margin-left: 0;
  }
}

.plus:nth-last-of-type(n+3) {
  margin-bottom: 6.35rem;
}

@media (max-width: 1199px) {
  .plus:nth-last-of-type(n+2) {
    margin-bottom: 3.2rem;
  }
}

@media (max-width: 767px) {
  .plus:nth-last-of-type(n+2) {
    margin-bottom: 3rem;
  }
}

@media (max-width: 1199px) {
  .plus:last-child {
    flex-grow: 0;
    flex-basis: 48.5%;
  }
}

@media (max-width: 767px) {
  .plus:last-child {
    flex-basis: 100%;
